___CSS_LOADER_EXPORT___.push([module.id, ".map[data-v-8ea77e08]{height:500px;width:100%}", "",{"version":3,"sources":["webpack://./resources/js/Components/MapWithPolygons.vue"],"names":[],"mappings":"AA+DA,sBAEI,YAAa,CADb,UAEJ","sourcesContent":["<template>\n    <div class=\"map\"/>\n</template>\n\n<script>\nimport GoogleMapsService from '../Services/GoogleMapsService'\n\nexport default {\n    name: \"MapWithPolygons\",\n    props: {\n        apiKey: {\n            type: String,\n            required: true\n        },\n        mapCenter: {\n            type: Object,\n            default: {lat: 0, lng: 0}\n        },\n        polygons: {\n            type: Array,\n            required: true\n        }\n    },\n    async mounted() {\n        try {\n            const google = await GoogleMapsService(this.apiKey);\n            const polygons = this.getPolygons();\n            const map = new google.maps.Map(this.$el, {\n                zoom: 8,\n                center: this.mapCenter,\n                mapTypeControl: false,\n                streetViewControl: false\n            });\n\n            polygons.map(polygon => {\n                polygon.setMap(map);\n            })\n        } catch (error) {\n            console.error(error);\n        }\n    },\n    methods: {\n        getPolygons() {\n            const polygons = [];\n\n            this.polygons.map(polygon => {\n                polygons.push(new google.maps.Polygon({\n                    paths: polygon.coords,\n                    strokeColor: polygon.color,\n                    strokeOpacity: 0.8,\n                    strokeWidth: 2,\n                    fillColor: polygon.color,\n                    fillOpacity: 0.35\n                }));\n            });\n\n            return polygons;\n        }\n    }\n}\n</script>\n\n<style scoped>\n.map {\n    width: 100%;\n    height: 500px;\n}\n</style>\n"],"sourceRoot":""}]);
